@import '../../../public/styles/main-styles.scss';

.footer {
  padding: 20px 16px;
  color: $black;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 10fr 1fr;
}

.footer__columns {
  width: 90%;
  display: flex;
  margin: 60px auto 40px;
}

.footer__column {
  display: flex;
  line-height: 30px;
  font-size: 18px;
  flex: 1;
  align-items: flex-end;
}

.footer__column_content_copyright {
  flex: 2;
  position: relative;
  align-items: flex-start;
}

.footer__social-icon {
  margin: 0;
}

.footer__column-links {
  display: flex;
  flex-direction: column;
}

.footer__column-heading {
  font-weight: bold;
  line-height: 30px;
  font-size: 18px;
  margin: 0 0 20px;
}

.footer__column_content_info {
  align-items: flex-end;
}


.footer__link {
  color: $black;
}

.logo_place_footer {
  width: 183px;
  height: 35px;
}

.footer__author {
  margin: 0;
  align-self: end;
}

.logo__place_footer {
  position: absolute;
  left: 0px;
  top: 0px;
}

.social-icons {
  width: 16px;
  height: 16px;
}
