@import './public/styles/main-styles.scss';

.card {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 40px;
  border:  1px solid $black;
  width: min-content;
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
  transition: transform ease-in 0.2s;

  &:hover {
    transform: scale(1.03);
  }

  &__img-container {
    position: relative;
  }

  &__title {
    margin-bottom: 5px;
  }

  &__price {
    transition: opacity ease-in 0.3s;
    opacity: 1;
  }

  &__info {
    transition: 0.3s;
  }

  &__button {
    cursor: pointer;
    outline: none;
    opacity: 1;
    transition: 0.3s;

    &:hover {
      background-color: rgba(186, 176, 176, 0.8);;
    }
  }

  &__button-container {
    position: absolute;
    display: grid;
    grid-auto-flow: column;
    grid-gap: 10px;
    bottom: -60px;
    left: 8px;
    transition: 0.3s;
    padding: 20px 0;
    transform: translate(0, 0);
  }

  &:hover .card__price {
    opacity: 1;
  }

  &:hover .card__button {
    opacity: 1;
    transform: translate(0, -60px);
  }

  &:hover .card__info {
    transform: translate(0, -20px);
  }
}
